export default {
    data() {
        return {
            menus: [
                {
                    url: '/',
                    title: 'صفحه اصلی',
                },
                {
                    url: '/shop',
                    title: 'خطوط تولید',
                },
                {
                    url: '/',
                    title: 'دپارتمان ها',
                    submenu: [
                        {
                            url: '/',
                            title: 'فنی',
                        },
                        {
                            url: '/',
                            title: 'خدمات پس از فروش و پشتیبانی',
                        },
                        {
                            url: '/',
                            title: 'آموزش',
                        },
                        {
                            url: '/',
                            title: 'بازاریابی و فروش',
                        },
                        {
                            url: '/',
                            title: 'کسب و کار',
                        }
                    ]
                },
                {
                    url: '/blog',
                    title: 'وبلاگ',
                },
              {
                url: '/about',
                title: 'درباره ما',
              },
                {
                    url: '/contact',
                    title: 'تماس با ما',
                },
            ]
        }
    },
    mounted() {
        let getSiblings = function (e) {
            let siblings = [];
            if(!e.parentNode) {
                return siblings;
            }
            let sibling  = e.parentNode.firstChild;
            while (sibling) {
                if (sibling.nodeType === 1 && sibling !== e) {
                    siblings.push(sibling);
                }
                sibling = sibling.nextSibling;
            }
            return siblings;
        };

        const subMenuToggle = document.querySelectorAll('.submenu-toggle');
        subMenuToggle.forEach(function(btn) {
            btn.addEventListener('click', function() {
                if(!this.classList.contains('active')) {
                    this.classList.add('active')
                    this.nextElementSibling.classList.add('active')
                    this.closest('li').classList.add('active')
                    getSiblings(this.closest('li')).forEach(function(item) {
                        item.classList.remove('active')
                        item.querySelectorAll('li, .submenu-toggle, .submenu').forEach(function(child) {
                            child.classList.remove('active')
                        })
                    })
                } else {
                    this.closest('li').classList.remove('active')
                    this.closest('li').querySelectorAll('li, .submenu-toggle, .submenu').forEach(function(child) {
                        child.classList.remove('active')
                    })
                }
            })
        })
    }
}
